.widgets{
    flex: 0.2;
    position: sticky;
    top: 80px;
    background-color: white;
    border-radius: 10px;
    border: 1px solid lightgray;
    height: fit-content;
    padding-bottom: 10px;
}

.widgets__top{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    padding: 10px;
}

.widgets__top > h2 {
    font-size: 16px;
    font-weight: 400;
}

.widgets__article{
    display: flex;
    padding: 10px;
    cursor: pointer;
}

.widgets__article:hover{
    background-color: whitesmoke;
}

.widgets__articleLeft > .MuiSvgIcon-root{
    font-size: 15px;
}

.widgets__articleLeft{
    color: #0177b7;
    margin-right: 5px;
}


.widgets__articleRight{
    flex: 1;
}

.widgets__articleRight > h4 {
    font-size: 14px;
}

.widgets__articleRight > p {
    font-size: 12px;
    color: gray;    
}

